// import { BaseImgData } from '@/types/components/baseImg.interface';

@Component({
  // data(): BaseImgData {
  //   return {
  //     msg: 'BaseImg'
  //   };
  // }
})
export default class BaseImg extends Vue {
  // msg!: string;

  @Prop() private alt!: string; //  
  @Prop() private eager!: boolean; //  DOM  SEO

  @Prop() private src!: string | object; //  URL
  @Prop() private lazySrc!: string; // base64 vuetify-loader 
  @Prop() private gradient!: string; //   linear-gradient 
  @Prop() private aspectRatio!: string | number; // width/height 1920x1080px  1.7778 
  @Prop() private contain!: boolean; // 
  @Prop() private position!: string; //   background-position

  @Prop() private width!: number | string; // 
  @Prop() private height!: number | string; // 
  @Prop() private minWidth!: number | string; // 
  @Prop() private maxWidth!: number | string; // 
  @Prop() private minHeight!: number | string; // 
  @Prop() private maxHeight!: number | string; // 

  @Prop() private options!: object; //
  @Prop() private sizes!: string; //  srcset MDN
  @Prop() private srcset!: string; // 
  @Prop() private transition!: boolean | string; //  lazy-src  src 

  @Prop() private bgColor!: string; // 

  // @Prop({ default: true }) private loading!: boolean; // flag
  @Prop({ default: 'fontColor' }) private loadingColor!: string; // 
  @Prop({ default: 24 }) private loadingSize!: number | string; // 
  @Prop({ default: 3 }) private loadingWidth!: number | string; // 

  @Prop({ default: 'fontColor' }) private loadingIconColor!: string; // 
  @Prop({ default: 48 }) private loadingIconSize!: number | string; // 
  @Prop({ default: 'D3III20glBasic icon-20gl-picture' })
  private loadingIconClass!: string; // 

  @Prop({ default: false }) private loadingFailed!: boolean; // flag
  @Prop({ default: 'fontColor' }) private loadingFailedIconColor!: string; // 
  @Prop({ default: 44 }) private loadingFailedIconSize!: number | string; // 
  @Prop({ default: 'D3III20glBasic icon-20gl-pictureSplit' })
  private loadingFailedIconClass!: string; // 
}
