






























































import { Vue, Component, Prop } from 'vue-property-decorator';
// import { BaseAvatarData } from '@/types/components/baseAvatar.interface';
import BaseImg from '@/components/BaseImg.vue';

@Component({
  components: {
    BaseImg
  }

  // data(): BaseAvatarData {
  //   return {
  //     msg: 'BaseAvatar'
  //   };
  // }
})
export default class BaseAvatar extends Vue {
  // msg!: string;

  @Prop({ default: 'borderColor4' }) private color!: string; // 
  @Prop() private tile!: boolean; //  border-radius

  @Prop() private left!: boolean; //  v-chip  v-btn 
  @Prop() private right!: boolean; //  v-chip  v-btn 

  @Prop({ default: 48 }) private size!: number | string; // 
  @Prop() private width!: number | string; // 
  @Prop() private height!: number | string; // 
  @Prop() private minWidth!: number | string; // 
  @Prop() private maxWidth!: number | string; // 
  @Prop() private minHeight!: number | string; // 
  @Prop() private maxHeight!: number | string; // 

  // 
  @Prop() private src!: string | object; //  URL
  @Prop({ default: require('@/assets/svgs/default-account.svg') }) private lazySrc!: string; // base64 vuetify-loader 
  @Prop({ default: require('@/assets/svgs/default-account-split.svg') })
  private loadingFailedImg!: string; // 

  @Prop({ default: 'fontColor' }) private loadingColor!: string; // 
  // @Prop({ default: 20 }) private loadingSize!: number | string; // 
  // @Prop({ default: 2 }) private loadingWidth!: number | string; // 

  @Prop({ default: 'white' }) private loadingIconColor!: string; // 
  // @Prop({ default: 32 }) private loadingIconSize!: number | string; // 
  @Prop({ default: 'mdi-account' })
  private loadingIconClass!: string; // 

  @Prop({ default: false }) private loadingFailed!: boolean; // flag
  @Prop({ default: 'white' }) private loadingFailedIconColor!: string; // 
  // @Prop({ default: 32 }) private loadingFailedIconSize!: number | string; // 
  @Prop({ default: 'mdi-account' })
  private loadingFailedIconClass!: string; // 

  // 
  @Prop() private icon!: string; // 
  @Prop({ default: 'white' }) private iconColor!: string; // 
  @Prop() private iconDark!: boolean; // 
  @Prop() private iconLight!: boolean; // 

  // 
  @Prop() private text!: string; // 
  @Prop({ default: 'white--text' }) private textClass!: string; // 

  //  - 
  get loadingSize() {
    return Number(this.size) * 0.6; // 0.65
  }

  //  - 
  get loadingWidth() {
    return Number(this.size) * 0.05;
  }

  //  - 
  get loadingIconSize() {
    return Number(this.size) * 0.6; // 0.75
  }

  //  - 
  get loadingFailedIconSize() {
    return Number(this.size) * 0.6; // 0.75
  }

  //  - 
  get iconSize() {
    return Number(this.size) * 0.6; // 0.75
  }

  //  - 
  get textSize() {
    return `${Number(this.size) * 0.38}px`;
  }
}
